import React from 'react'
import TweenOne from 'rc-tween-one'
import { Link } from 'rc-scroll-anim'
import { Menu, Dropdown, Icon } from 'antd'

class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      phoneOpen: false,
      menuHeight: 0,
      lan: ['CN', 'EN'],
      selectLan: 'CN'
    }
    this.menu = React.createRef()
  }

  /*
  componentDidMount() {
    // 如果是 react 16.3 以下版本请使用 findDOMNode;
    this.menuDom = findDOMNode(this.menu);
  }
  */

  phoneClick = () => {
    const phoneOpen = !this.state.phoneOpen
    this.setState({
      phoneOpen,
      menuHeight: phoneOpen ? this.menu.current.dom.scrollHeight : 0
    })
  }
  handleSizeChange = e => {
    this.setState({ selectLan: e.key })
  }
  render() {
    const { ...props } = this.props
    const { dataSource, isMobile } = props
    const { lan, selectLan } = this.state
    const menu = (
      <Menu onClick={this.handleSizeChange}>
        {lan.map((key, i) => (
          <Menu.Item key={key}>
            <a target="_blank" rel="noopener noreferrer">
              {key}
            </a>
          </Menu.Item>
        ))}
      </Menu>
    )
    delete props.dataSource
    delete props.isMobile
    const { menuHeight, phoneOpen } = this.state
    const navData = dataSource.Menu.children
    const navChildren = Object.keys(navData).map((key, i) => (
      <Link
        key={i.toString()}
        {...navData[key]}
        onClick={() => {
          this.phoneClick()
          this.setState({ phoneOpen: false })
        }}
      >
        {navData[key].children}
      </Link>
    ))
    return (
      <TweenOne
        component="header"
        animation={{ opacity: 0, type: 'from' }}
        {...dataSource.wrapper}
        {...props}
      >
        <div
          {...dataSource.page}
          className={`${dataSource.page.className}${phoneOpen ? ' open' : ''}`}
        >
          <TweenOne
            animation={{ x: -30, type: 'from', ease: 'easeOutQuad' }}
            {...dataSource.logo}
          >
            <img width="100%" src={require('./img/logo.png')} alt="img" />
          </TweenOne>
          <div className="pull-right nav-item">
            <img src={require('./img/ico-search.png')} alt="" className="ico" />
            <img
              onClick={() => {
                window.location.href = 'https://portal.cpucloud.com'
              }}
              src={require('./img/ico-person.png')}
              alt=""
              className="ico person"
            />
            <Dropdown overlay={menu}>
              <a className="ant-dropdown-link bold" href="#">
                {selectLan} <Icon type="down" />
              </a>
            </Dropdown>
          </div>
          {isMobile && (
            <div
              {...dataSource.mobileMenu}
              onClick={() => {
                this.phoneClick()
              }}
            >
              <em />
              <em />
              <em />
            </div>
          )}
          <TweenOne
            {...dataSource.Menu}
            animation={{ x: 30, type: 'from', ease: 'easeOutQuad' }}
            ref={this.menu} // {(c) => { this.menu = c; }}
            style={isMobile ? { height: menuHeight } : null}
          >
            {navChildren}
          </TweenOne>
        </div>
      </TweenOne>
    )
  }
}

export default Header
