import React from 'react';
import TweenOne from 'rc-tween-one';
import OverPack from 'rc-scroll-anim/lib/ScrollOverPack';
import QueueAnim from 'rc-queue-anim';
import { Row, Col } from 'antd';
import { isImg } from './utils';

class Footer extends React.Component {
  static defaultProps = {
    className: 'footer1',
  };

  getLiChildren = (data,isMobile) =>
    data.map((item, i) => {
      return isMobile&&i >= 1 ? null : (
        <Col key={i.toString()} {...item} title={null} content={null}>
          <h2 className="footTitle" {...item.title}>
            {typeof item.title.children === 'string' &&
            item.title.name == "image" ? (
              <img width="100%" src={require("./img/logo.png")} alt="img" />
            ) : (
              item.title.children
            )}
          </h2>
          {
            item.title.name == "image" ? (
              <div className="flex between icoBtn">
              <img src={require("./img/weibo.png")} alt=""/>
              <img src={require("./img/ico-facebook.png")} alt=""/>
              <img src={require("./img/ico-tewwn.png")} alt=""/>
              <img src={require("./img/ico-wechat.png")} alt=""/>
            </div>
            ) :  <div {...item.content}>{item.content.children}</div>
          }
        </Col>
      );
    });

  render() {
    const { ...props } = this.props;
    const { dataSource,isMobile } = props;
    delete props.dataSource;
    delete props.isMobile;
    const childrenToRender = this.getLiChildren(dataSource.block.children,isMobile);
    console.log(this.props)
    return (
      
      
      <div {...props} {...dataSource.wrapper}>
        <OverPack {...dataSource.OverPack}>
          <QueueAnim
            type="bottom"
            key="ul"
            leaveReverse
            component={Row}
            {...dataSource.block}
          >
            {childrenToRender}
          </QueueAnim>
          <TweenOne
            animation={{ y: '+=30', opacity: 0, type: 'from' }}
            key="copyright"
            {...dataSource.copyrightWrapper}
          >
            <div {...dataSource.copyrightPage}>
              <div {...dataSource.copyright}>
                {isMobile? <div>©2019 融创云平台,Inc.</div> : dataSource.copyright.children 
                 
                }
              </div>
            </div>
          </TweenOne>
        </OverPack>
      </div>
    );
  }
}

export default Footer;
