/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Nav2 from './Nav2';
import Banner0 from './Banner0';
import Feature3 from './Feature3';
import Teams1 from './Teams1';
import Price from './Price';
import PriceDetail from './PriceDetail';
import Server from './Server';
import Content3 from './Content3';
import About from './About';
import Address from './Address';
import Footer1 from './Footer1';
import Feature1 from './Feature1';
import {
  Nav20DataSource,
  Banner01DataSource,
  Feature30DataSource,
  Teams10DataSource,
  PriceDataSource,
  PriceDetailDataSource,
  Content30DataSource,
  Footer10DataSource,
  Feature10DataSource,
  ServerDataSource,
  AboutDataSource,
  AddressDataSource
} from './data.source';
import './less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location } = window;

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      upShow: false,
      show: !location.port, // 如果不是 dva 2.0 请删除
    };
  }

  gotoUp(){
    let scrollToTop = window.setInterval(function() {
      let pos = window.pageYOffset;
      if ( pos > 0 ) {
          window.scrollTo( 0, pos - 80 ); // how far to scroll on each step
      } else {
          window.clearInterval( scrollToTop );
      }
  }, 1);
}
  componentDidMount() {
    // 适配手机屏幕;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 样式在组件渲染之后动态加载，导致滚动组件不生效；线上不影响；
    /* 如果不是 dva 2.0 请删除 start */
    if (location.port) {
      // 样式 build 时间在 200-300ms 之间;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /* 如果不是 dva 2.0 请删除 end */
    window.addEventListener('scroll' , ()=>{
      let scrollTop = document.documentElement.scrollTop;
      console.log(scrollTop)
      if(scrollTop > 200){
        this.setState({
          upShow : true
        })
      }else{
        this.setState({
          upShow : false
        })
      }
    })
  }

  render() {
    const children = [
      <Nav2
        id="Nav2_0"
        key="Nav2_0"
        dataSource={Nav20DataSource}
        isMobile={this.state.isMobile}
      />,
      <Banner0
        id="Banner0_1"
        key="Banner0_1"
        dataSource={Banner01DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature3
        id="Feature3_0"
        key="Feature3_0"
        dataSource={Feature30DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature1
        id="Feature1_0"
        key="Feature1_0"
        dataSource={Feature10DataSource}
        isMobile={this.state.isMobile}
      />,
      <Teams1
        id="Teams1_0"
        key="Teams1_0"
        dataSource={Teams10DataSource}
        isMobile={this.state.isMobile}
      />,
      <Price
        id="Price"
        key="Price"
        dataSource={PriceDataSource}
        isMobile={this.state.isMobile}
      />,
      <PriceDetail
        id="PriceDetail"
        key="PriceDetail"
        dataSource={PriceDetailDataSource}
        isMobile={this.state.isMobile}
      />,
      <Server
        id="Server"
        key="Server"
        dataSource={ServerDataSource}
        isMobile={this.state.isMobile}
      />,
      <Content3
        id="Content3_0"
        key="Content3_0"
        dataSource={Content30DataSource}
        isMobile={this.state.isMobile}
      />,
      <About
        id="About"
        key="About"
        dataSource={AboutDataSource}
        isMobile={this.state.isMobile}
      />,
      <Address
        id="Address"
        key="Address"
        dataSource={AddressDataSource}
        isMobile={this.state.isMobile}
      />,
      <Footer1
        id="Footer1_0"
        key="Footer1_0"
        dataSource={Footer10DataSource}
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/* 如果不是 dva 2.0 替换成 {children} start */}
        {this.state.show && children}
        {/* 如果不是 dva 2.0 替换成 {children} end */}
        <div>
          {
            this.state.upShow &&
              <img src={require("./img/backup.png")} alt="向上" className="backUp" onClick={this.gotoUp}/>
          }
        </div>
      </div>
    );
  }
}
